
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);//vue

//Vuex.Store 
const store = new Vuex.Store({
	state: {//
		clickedNav: '', // 
		activeItem: '', // 
	},
	mutations: {
		setClickedNav(state, val) {
			state.clickedNav = val
		},
		setActiveItem(state,val) {
			state.activeItem = val;
		},
	},
	actions: {
		
	},
})
export default store
